// Mock `widget` object
if (!window.widget || !window.widget.uwaUrl) {
  // Delay initialization to check if real widget is injected
  setTimeout(() => {
    if (!window.widget || !window.widget.uwaUrl) {
      console.log("Mock widget created");
      const Widget = function () {
        this.uwaUrl = window.location.origin + "/";
        console.log("widget.uwaUrl:", this.uwaUrl); // Log to ensure it's set correctly
        let events = {};
        this.addEvent = (event, callback) => {
          events[event] = callback;
          if (event === "onLoad") {
            if (document.readyState === "loading") {
              window.addEventListener("DOMContentLoaded", callback);
            } else {
              callback();
            }
          }
        };
        this.setTitle = (title) => {
          document.title = title;
        };
        this.dispatchEvent = () => {};
      };

      window.widget = new Widget();
      console.log("Mock widget created:", window.widget);
    } else {
      console.log("Real widget object detected. Mock not initialized.");
    }
  }, 200); // Adjust the delay if needed
} else {
  console.log("Real widget object detected. Skipping mock initialization.");
}



// Mock `define` and `require`
if (typeof define === 'undefined') {
  window.define = (name, deps, callback) => {
    console.log(`Defining module: ${name}`);
    callback();
  };
}

if (typeof requirejs === 'undefined') {
  window.requirejs = (modules, callback) => {
    console.warn("Mock RequireJS: Modules not found:", modules);
    callback(); // Continue execution even if modules are mocked
  };
}
