/* global __webpack_public_path__ */
import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import './setupMocks';
import reportWebVitals from './reportWebVitals';

// Set Webpack's public path dynamically if available
if (window.widget && window.widget.uwaUrl) {
  const path = window.widget.uwaUrl.substring(0, window.widget.uwaUrl.lastIndexOf("/") + 1);
  __webpack_public_path__ = path;
  console.log("Public path set to:", __webpack_public_path__);
} else {
  console.warn("widget.uwaUrl is missing. Using default public path './'");
  __webpack_public_path__ = "./";
}

// Initialize React app
function startReactApp() {
  const rootElement = document.getElementById('root') || document.createElement('div');
  if (!document.getElementById('root')) {
    rootElement.id = 'root';
    document.body.appendChild(rootElement);
  }

  const root = ReactDOM.createRoot(rootElement);
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

// Setup RequireJS
if (typeof define === 'undefined') {
  window.define = (name, deps, callback) => {
    if (typeof deps === 'function') callback = deps;
    callback();
  };
}

if (typeof requirejs === 'undefined') {
  window.requirejs = (modules, callback) => {
    console.warn("Mock RequireJS: Modules not found:", modules);
    callback(); // Continue execution even if modules are mocked
  };
}

// Start React app
startReactApp();

// Measure performance
reportWebVitals();
